import { tr } from "@/services/lang";

export default {
  data() {
    return {
      count: 0,
    };
  },
  computed: {
    lang() {
      return tr();
    },
  },
  mounted() {
    this.$nextTick(function () {
      if (localStorage.getItem("cartItems")) {
        this.count = JSON.parse(localStorage.getItem("cartItems")).length;
      }
    });
  },
};
