import aside_menu from "@/components/main/aside.js";
import api from "~/services/api";

export default {
  data() {
    return {
      menu: aside_menu,
        user : null
    }
  },
    created() {
        api.get('user/current', this.$cookies.get('token'))
            .then(res => {
                this.user = res.data.data
            })
    }
}
